import React, { Component } from 'react'
import injectSheet from 'react-jss'
import set from '../lib/set-value'
import { createMuiTheme } from '@material-ui/core/styles'
import { MuiThemeProvider } from '@material-ui/core/styles'

import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import Button from '@material-ui/core/Button'
import { CopyToClipboard } from 'react-copy-to-clipboard'

import PaletteSettingsPanel from '../PaletteSettingsPanel'
import TypographySettingsPanel from '../TypographySettingsPanel'
import Components from '../Components'

import styles from './styles'

export class _App extends Component {
  constructor(props) {
    super(props)
    this.state = {
      theme: {}
    }
    this.handleValueChanged = this.handleValueChanged.bind(this)
  }

  handleValueChanged(key, value) {
    this.setState({
      theme: set(this.state.theme, key, value)
    })
  }

  render() {
    const theme = createMuiTheme(this.state.theme)
    const { classes } = this.props
    console.log(this.state.theme)
    return (
      <div>
        <AppBar position="fixed" color="default">
          <Toolbar>
            <Typography variant="title" color="inherit">
              Material UI Next Theme Generator
            </Typography>
          </Toolbar>
        </AppBar>

        <Grid container spacing={16} className={classes.container}>
          <Grid item sm={8}>
            <MuiThemeProvider theme={theme}>
              <Components />
            </MuiThemeProvider>
          </Grid>
          <Grid item sm={4} className={classes.settingsPanel}>
            <CopyToClipboard text={JSON.stringify(this.state.theme)}>
              <Button className={classes.copyButton}>Copy Theme</Button>
            </CopyToClipboard>
            <PaletteSettingsPanel
              palette={theme.palette}
              onValueChanged={this.handleValueChanged}
            />
            <TypographySettingsPanel
              typography={theme.typography}
              onValueChanged={this.handleValueChanged}
            />
          </Grid>
        </Grid>
      </div>
    )
  }
}

const App = injectSheet(styles)(_App)
export default App




