import React, { Component } from 'react'
import injectSheet from 'react-jss'

import AppBar from '@material-ui/core/AppBar'
import Button from '@material-ui/core/Button'
import Typography from '@material-ui/core/Typography'
import Toolbar from '@material-ui/core/Toolbar'
import Card from '@material-ui/core/Card'
import CardActions from '@material-ui/core/CardActions'
import CardContent from '@material-ui/core/CardContent'
import Chip from '@material-ui/core/Chip'

import styles from './styles'

export class _Components extends Component {
  render() {
    const { classes } = this.props
    return (
      <div>
        <div>
          <Typography variant="headline" component="h2" gutterBottom={true}>App Bar:</Typography>
          <AppBar className={classes.appBar} position="static" color="primary">
            <Toolbar>
              <Typography variant="title" color="inherit">
                Primary App Bar
              </Typography>
            </Toolbar>
          </AppBar>
          <AppBar className={classes.appBar} position="static" color="secondary">
            <Toolbar>
              <Typography variant="title" color="inherit">
                Secondary App Bar
              </Typography>
            </Toolbar>
          </AppBar>
        </div>
        <div>
          <Typography variant="headline" component="h2" className={classes.componentTitle} gutterBottom={true}>Buttons:</Typography>
          <Button className={classes.button} color="primary">Primary Flat Button</Button>
          <Button className={classes.button} color="secondary">Secondary Flat Button</Button>
          <Button className={classes.button} disabled={true}>Disabled Flat Button</Button>
          <br />
          <Button className={classes.button} variant="outlined" color="primary">Primary Outlined Button</Button>
          <Button className={classes.button} variant="outlined" color="secondary">Secondary Outlined Button</Button>
          <Button className={classes.button} disabled={true}>Disabled Outlined Button</Button>
          <br />
          <Button className={classes.button} variant="raised" color="primary">Primary raised Button</Button>
          <Button className={classes.button} variant="raised" color="secondary">Secondary raised Button</Button>
          <Button className={classes.button} variant="raised" disabled={true}>Disabled raised Button</Button>
          <br />
          <Button className={classes.button} variant="fab" color="primary">+</Button>
          <Button className={classes.button} variant="fab" color="secondary">+</Button>
          <Button className={classes.button} variant="fab" disabled={true}>+</Button>
        </div>
        <div>
          <Typography variant="headline" component="h2" className={classes.componentTitle} gutterBottom={true}>Card:</Typography>
            <Card className={classes.card}>
              <CardContent>
                <Typography color="textSecondary">
                  Text Secondary
                </Typography>
                <Typography variant="headline" component="h2">
                  Headline
                </Typography>
                <Typography component="p">
                  Paragraph text
                </Typography>
              </CardContent>
              <CardActions>
                <Button size="small">Card Actions</Button>
              </CardActions>
            </Card>
        </div>
        <div>
          <Typography variant="headline" component="h2" className={classes.componentTitle} gutterBottom={true}>Chip:</Typography>
            <Chip label="Basic Chip" className={classes.chip} />
            <Chip label="Clickable Chip" clickable={true} className={classes.chip} />
        </div>
      </div>
    )
  }
}

const Components = injectSheet(styles)(_Components)
export default Components




