export default {
  sectionIcon: {
    marginRight: '16px'
  },
  sectionTitle: {
    lineHeight: '24px'
  },
  settingTitle: {
    textTransform: 'capitalize'
  },
  typographyTitle: {
    textTransform: 'capitalize',
    margin: '12px 0',
    fontWeight: '600'
  },
  sectionDetails: {
    flexDirection: 'column',
    height: 'calc(100vh - 270px)',
    overflow: 'scroll'
  },
  colorPreview: {
    width: '20px',
    height: '20px',
    marginRight: '12px'
  }
}



