import React from 'react'
import PropTypes from 'prop-types'
import injectSheet from 'react-jss'

import isColor from 'is-color'
import { ChromePicker } from 'react-color'

import Avatar from '@material-ui/core/Avatar'
import ExpansionPanel from '@material-ui/core/ExpansionPanel'
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary'
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails'
import Typography from '@material-ui/core/Typography'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore'
import TextField from '@material-ui/core/TextField'
import TypographyIcon from '@material-ui/icons/TextFields'

import styles from './styles'

const renderSettingExpansionPanel = (key, value, children, classes) => {
  const title = key.replace(/([A-Z]+)/g, " $1").replace(/([A-Z][a-z])/g, " $1")

  return (
    <ExpansionPanel key={key}>
      <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
        {
          isColor(value) && <Avatar className={classes.colorPreview} style={{ backgroundColor: value}} />
        }
        <Typography className={classes.settingTitle}>{title}</Typography>
      </ExpansionPanelSummary>
      <ExpansionPanelDetails>
        {children}
      </ExpansionPanelDetails>
    </ExpansionPanel>
  )
}

const renderTypographySetting= (object, onValueChanged, prefix, classes) => {
  let settings = []

  Object.keys(object).forEach(key => {
    const value = object[key]
    const type = typeof object[key]
    let item

    if (type === 'object') {
      item = (
        <div key={key}>
          <Typography className={classes.typographyTitle}>{key}:</Typography>
          {renderTypographySetting(value, onValueChanged, key, classes)}
        </div>
      )
      settings.push(item)
    } else if (type === 'string' && isColor(value)) {
      settings.push(
        renderSettingExpansionPanel(
          key,
          value,
          <ChromePicker
            className={classes.picker}
            color={value}
            onChangeComplete={(color) => onValueChanged(`typography.${prefix}.${key}`, color.hex)}
          />,
          classes
        )
      )
    } else if (type === 'string') {
      settings.push(
        renderSettingExpansionPanel(
          key,
          value,
          <TextField
            value={value}
            onChange={(event) => onValueChanged(`typography.${prefix}.${key}`, event.target.value)}
          />,
          classes
        )
      )

    }
  })
  return settings
}

export const _TypographySettingsPanel = ({ typography, onValueChanged, classes }) => {
  const settings = renderTypographySetting(typography, onValueChanged, '', classes)

  return (
    <ExpansionPanel>
      <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
        <TypographyIcon className={classes.sectionIcon} />
        <Typography className={classes.sectionTitle}>Typography</Typography>
      </ExpansionPanelSummary>
      <ExpansionPanelDetails className={classes.sectionDetails}>
        {settings}
      </ExpansionPanelDetails>
    </ExpansionPanel>
  )
}

_TypographySettingsPanel.propTypes = {
  classes: PropTypes.object.isRequired,
  onValueChanged: PropTypes.func.isRequired,
  typography: PropTypes.object.isRequired
}

const TypographySettingsPanel = injectSheet(styles)(_TypographySettingsPanel)
export default TypographySettingsPanel




