import React from 'react'
import PropTypes from 'prop-types'
import injectSheet from 'react-jss'

import isColor from 'is-color'
import { ChromePicker } from 'react-color'

import Avatar from '@material-ui/core/Avatar'
import ExpansionPanel from '@material-ui/core/ExpansionPanel'
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary'
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails'
import Typography from '@material-ui/core/Typography'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore'
import PaletteIcon from '@material-ui/icons/Palette'
import Slider from '@material-ui/lab/Slider'

import styles from './styles'

const renderSettingExpansionPanel = (key, value, title, children, classes) => {

  title = title.replace(/([A-Z]+)/g, " $1").replace(/([A-Z][a-z])/g, " $1")
  return (
    <ExpansionPanel key={key}>
      <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
        {
          isColor(value) && <Avatar className={classes.colorPreview} style={{ backgroundColor: value}} />
        }
        <Typography className={classes.settingTitle}>{title}</Typography>
      </ExpansionPanelSummary>
      <ExpansionPanelDetails>
        {children}
      </ExpansionPanelDetails>
    </ExpansionPanel>
  )
}

const renderPaletteSettings = (object, onValueChanged, prefix, classes) => {
    const colorsToSet = ['action', 'background', 'text', 'grey', 'common']
    const colorsWithSingleValue = ['error', 'primary', 'secondary']

    let settings = []
    const titlePrefix = prefix === '' ? '' : `${prefix}: `
    Object.keys(object).forEach(key => {
      const value = object[key]
      const type = typeof object[key]

      if (type === 'object') {
        if (colorsToSet.includes(key)) {
          settings.push(renderPaletteSettings(value, onValueChanged, key, classes))
        } else if (colorsWithSingleValue.includes(key)) {
          settings.push(
            renderSettingExpansionPanel(
              key,
              value['main'],
              `${titlePrefix} ${key} Color`,
              <ChromePicker
                className={classes.picker}
                color={value['main']}
                onChangeComplete={(color) => onValueChanged(`palette.${key}.main`, color.hex)}
              />,
              classes
            )
          )
        }
      }
      else if (type === 'number') {
        const path = prefix === '' ? `palette.${key}` : `palette.${prefix}.${key}`
        settings.push(
          renderSettingExpansionPanel(
            key,
            value,
            `${titlePrefix} ${key}`,
            <Slider value={value*100} onChange={(e, value) => onValueChanged(path, value/100)} />,
            classes
          )
        )
      }
      else if (type === 'string' && isColor(value)) {
        settings.push(
          renderSettingExpansionPanel(
            key,
            value,
            `${titlePrefix} ${key} Color`,
            <ChromePicker
              className={classes.picker}
              color={value}
              onChangeComplete={(color) => onValueChanged(`palette.${prefix}.${key}`, color.hex)}
            />,
            classes
          )
        )
      }
    })
    return settings
}

export const _PaletteSettingsPanel = ({ palette, onValueChanged, classes }) => {
  return (
    <ExpansionPanel
      classes={{
        root: classes.paletteSettingsPanel
      }}
    >
      <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
        <PaletteIcon className={classes.sectionIcon} />
        <Typography className={classes.sectionTitle}>Palette</Typography>
      </ExpansionPanelSummary>
      <ExpansionPanelDetails className={classes.sectionDetails}>
        {renderPaletteSettings(palette, onValueChanged, '', classes)}
      </ExpansionPanelDetails>
    </ExpansionPanel>
  )
}

_PaletteSettingsPanel.propTypes = {
  classes: PropTypes.object.isRequired,
  onValueChanged: PropTypes.func.isRequired,
  palette: PropTypes.object.isRequired
}

const PaletteSettingsPanel = injectSheet(styles)(_PaletteSettingsPanel)
export default PaletteSettingsPanel




